import React from "react";
import PropTypes from "prop-types";

import NavItem from "./NavItem/NavItem";

import "./navigation.scss";

const Navigation = (props) => {
  return (
    <nav className={`nav ${props.open ? "is-open" : "is-hidden"}`}>
      <ul className="nav__list">
        <NavItem link="/">Home</NavItem>
        <NavItem link="/our-story">Our Story</NavItem>
        <NavItem link="/venue">Venue</NavItem>
        <NavItem link="/travel">Travel</NavItem>
        <NavItem link="/things-to-do">Things to Do</NavItem>
        <NavItem link="/faq">FAQ</NavItem>
        <NavItem link="/rsvp">RSVP</NavItem>
      </ul>
    </nav>
  );
};

Navigation.propTypes = {
  open: PropTypes.bool.isRequired,
};

export default Navigation;
